import Mock from 'mockjs'

let initData = Mock.mock(
  {
    'data|1': [
      {
        'titile': 'Air-conditioner',
        'hasSensor|1': !0,
        'currentMode': 'temperatureDown',
        'temperature|1-100.1': 65.5,
        'target|1-100.1': 10.0,
        'switch|1': !0,
        'humidity|1-100.1': 65.5
      },
      {
        'titile': 'Air-conditioner',
        'hasSensor|1': !0,
        'currentMode': 'temperatureUp',
        'temperature|1-100.1': 65.5,
        'target|1-100.1': 10.0,
        'switch|1': !0,
        'humidity|1-100.1': 65.5
      },
      {
        'titile': 'Air-conditioner',
        'hasSensor|1': !0,
        'currentMode': 'humidityDown',
        'temperature|1-100.1': 65.5,
        'target|1-100.1': 10.0,
        'switch|1': !0,
        'humidity|1-100.1': 65.5
      },
      {
        'titile': 'Air-conditioner',
        'hasSensor|1': !0,
        'currentMode': 'humidityUp',
        'temperature|1-100.1': 65.5,
        'target|0-100.1': 10.0,
        'switch|1': !0,
        'humidity|1-100.1': 65.5
      },
      {
        'titile': 'Air-conditioner',
        'hasSensor|1': !0,
        'currentMode': 'time',
        'onTime': Mock.Random.date('hh:mm:ss'),
        'offTime': Mock.Random.date('hh:mm:ss'),
        'switch|1': !0
      }
    ]
  }
)
let modeData = Mock.mock({
  'temperatureDown': {
    'titile': 'Air-conditioner',
    'hasSensor|1': !0,
    'currentMode': 'temperatureDown',
    'temperature|1-100.1': 65.5,
    'target|1-100.1': 10.0,
    'switch|1': !0,
    'humidity|1-100.1': 65.5
  },
  'temperatureUp': {
    'titile': 'Air-conditioner',
    'hasSensor|1': !0,
    'currentMode': 'temperatureUp',
    'temperature|1-100.1': 65.5,
    'target|1-100.1': 10.0,
    'switch|1': !0,
    'humidity|1-100.1': 65.5
  },
  'humidityUp': {
    'titile': 'Air-conditioner',
    'hasSensor|1': !0,
    'currentMode': 'humidityUp',
    'temperature|1-100.1': 65.5,
    'target|1-100.1': 10.0,
    'switch|1': !0,
    'humidity|1-100.1': 65.5
  },
  'humidityDown': {
    'titile': 'Air-conditioner',
    'hasSensor|1': !0,
    'currentMode': 'humidityDown',
    'temperature|1-100.1': 65.5,
    'target|1-100.1': 10.0,
    'switch|1': !0,
    'humidity|1-100.1': 65.5
  },
  'time': {
    'titile': 'Air-conditioner',
    'hasSensor|1': !0,
    'currentMode': 'time',
    'onTime': Mock.Random.date('hh:mm:ss'),
    'offTime': Mock.Random.date('hh:mm:ss'),
    'switch|1': !0
  }
})
export default {
  deviceControlInit: config => {
    console.log(config)
    let data = config.body ? modeData[config.body] : initData.data
    return data
  }
}



