const user = {
  state: {
    name: 'Tom',
    lastName: 'Curise',
    tittle: null, // rooms page's head tittle
    addeivePath: null // add devives 入口 设备列表或房间
  },
  mutations: {
    USER_NAME: (state, name) => {
      state.name = name
    },
    SUR_NAME: (state, name) => {
      state.lastName = name
    },
    ROOM_NAME: (state, name) => {
      state.tittle = name
    },
    ADDEVICE_PATH: (state, str) => {
      state.addeivePath = str
    }
  },
  actions: {
    userName ({ commit }, name) {
      commit('USER_NAME', name)
    },
    surName ({ commit }, name) {
      commit('SUR_NAME', name)
    },
    roomName ({ commit }, name) {
      commit('ROOM_NAME', name)
    },
    addevicePath ({ commit }, str) {
      commit('ADDEVICE_PATH', str)
    }
  },
  getters: {
    roomName (state) {
      return state.tittle
    },
    addevicePath (state) {
      return state.addeivePath
    },
    getuserName (state) {
      return state.name
    },
    getsurName (state) {
      return state.lastName
    }
  }
}

export default user



