import Vue from 'vue'
import Vuex from 'vuex'
import getters from './getters'
import errorLog from './modules/errorLog'
import plug from './modules/plug'
import user from './modules/user'
Vue.use(Vuex)

const store = new Vuex.Store({
  modules: {
    errorLog,
    user,
    plug
  },
  getters
})

export default store



