import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/home',
      component: () => import('@/views/home/index')
    },
    {
      path: '/login',
      component: () => import('@/page/login/login'),
      children: [
        {
          name: 'forgot',
          path: 'forgot',
          component: () => import('@/page/login/forgot')
        },
        {
          name: 'register',
          path: 'register',
          component: () => import('@/page/login/register')
        },
        {
          name: 'forgotStep',
          path: '/login/forgotStep',
          component: () => import('@/page/login/forgotStep')
        },
        {
          name: 'registerNext',
          path: '/login/registerNext',
          component: () => import('@/page/login/registerNext')
        }
      ]
    },
    {
      path: '/Me',
      name: 'me',
      component: () => import('@/page/me/me'),
      children: [
        {
          name: 'Rate',
          path: 'Rate',
          component: () => import('@/page/me/Rate')
        },
        {
          name: 'currency',
          path: 'currency',
          component: () => import('@/page/me/currency')
        },
        {
          name: 'aboutus',
          path: 'aboutus',
          component: () => import('@/page/me/aboutus')
        },
        {
          name: 'changePassword',
          path: 'changePassword',
          component: () => import('@/page/me/changePassword')
        },
        {
          name: 'changeProfile',
          path: 'changeProfile',
          component: () => import('@/page/me/changeProfile')
        },
        {
          name: 'changename',
          path: 'changename',
          component: () => import('@/page/me/changename')
        },
        {
          name: 'changesurname',
          path: 'changesurname',
          component: () => import('@/page/me/changesurname')
        },
        {
          name: 'addevice',
          path: 'addevice',
          component: () => import('@/page/deviceList/addevice/addevices')
        },
        {
          name: 'configwifi',
          path: 'configwifi',
          component: () => import('@/page/deviceList/addevice/configwifi')
        },
        {
          name: 'connectRoute',
          path: 'connectRoute',
          component: () => import('@/page/deviceList/addevice/connectRoute')
        },
        {
          name: 'choose',
          path: 'choose',
          component: () => import('@/page/deviceList/addevice/choose')
        },
        {
          name: 'configureFailed',
          path: 'configureFailed',
          component: () => import('@/page/deviceList/addevice/configureFailed')
        },
        {
          name: 'devicename',
          path: 'devicename',
          component: () => import('@/page/deviceList/addevice/deviceName')
        },
        {
          name: 'adsuccess',
          path: 'adsuccess',
          component: () => import('@/page/deviceList/addevice/adsuccess')
        },
        {
          name: 'adfailed',
          path: 'adfailed',
          component: () => import('@/page/deviceList/addevice/adfailed')
        },
        {
          name: 'Noconnectwifi',
          path: 'Noconnectwifi',
          component: () => import('@/page/deviceList/addevice/Noconnectwifi')
        },
        {
          name: 'addRoom',
          path: 'addRoom',
          component: () => import('@/page/roomList/addRoom')
        },
        {
          name: 'power',
          path: 'power',
          component: () => import('@/page/roomList/power')
        },
        {
          name: 'rooms',
          path: 'rooms',
          component: () => import('@/page/roomList/rooms'),
          children: [
            {
              path: '/temperatureRoom',
              name: 'temperature',
              component: () => import('@/page/deviceControl/temperature/index'),
              children: [{
                name: 'setTarget',
                path: 'setTarget',
                component: () => import('@/page/deviceControl/temperature/setTarget')
              },
              {
                name: 'timerMode',
                path: 'timerMode',
                component: () =>
                  import('@/page/deviceControl/temperature/timerMode')
              }
              ]
            }
          ]
        },
        {
          path: '/temperature',
          name: 'temperature',
          component: () => import('@/page/deviceControl/temperature/index'),
          children: [{
            name: 'setTarget',
            path: 'setTarget',
            component: () => import('@/page/deviceControl/temperature/setTarget')
          },
          {
            name: 'timerMode',
            path: 'timerMode',
            component: () => import('@/page/deviceControl/temperature/timerMode')
          }
          ]
        }
      ]
    }
  ]
})



